import React from 'react'
import { DndProvider, useDrop, useDrag } from 'react-dnd'
import { HTML5Backend } from 'react-dnd-html5-backend'

export default function Index() {
  return (
    <DndProvider backend={HTML5Backend}>
      <div className="m-2">
        <h1 className="text-2xl font-bold">Remix Drag and Drop Example</h1>
        <div>
          <div style={{ overflow: 'hidden', clear: 'both' }}>
            <Dustbin />
          </div>
          <div style={{ overflow: 'hidden', clear: 'both' }}>
            <Box name="Glass" />
            <Box name="Banana" />
            <Box name="Paper" />
          </div>
        </div>
      </div>
    </DndProvider>
  )
}

const ItemTypes = {
  BOX: 'box',
}

function Dustbin() {
  const [{ canDrop, isOver }, drop] = useDrop(() => ({
    accept: ItemTypes.BOX,
    drop: () => ({ name: 'Dustbin' }),
    collect: (monitor) => ({
      isOver: monitor.isOver(),
      canDrop: monitor.canDrop(),
    }),
  }))

  const isActive = canDrop && isOver
  let backgroundColor = 'bg-gray-700'
  if (isActive) {
    backgroundColor = 'bg-green-500'
  } else if (canDrop) {
    backgroundColor = 'bg-blue-500'
  }

  return (
    <div
      ref={drop}
      role={'Dustbin'}
      className={`h-48 w-48 mr-6 mb-6 text-white float-left text-center ${backgroundColor}`}
    >
      {isActive ? 'Release to drop' : 'Drag a box here'}
    </div>
  )
}

export interface BoxProps {
  name: string
}

interface DropResult {
  name: string
}

function Box({ name }: { name: string }) {
  const [{ isDragging }, drag] = useDrag(() => ({
    type: ItemTypes.BOX,
    item: { name },
    end: (item, monitor) => {
      const dropResult = monitor.getDropResult<DropResult>()
      if (item && dropResult) {
        console.log(`You dropped ${item.name} into ${dropResult.name}!`)
      }
    },
    collect: (monitor) => ({
      isDragging: monitor.isDragging(),
      handlerId: monitor.getHandlerId(),
    }),
  }))

  const opacity = isDragging ? 0.4 : 1
  return (
    <div
      ref={drag}
      role="Box"
      className={`border-dashed border-gray-500 bg-white py-2 px-4 mr-6 mb-6 cursor-move float-left`}
      data-testid={`box-${name}`}
    >
      {name}
    </div>
  )
}
